import React from 'react';
import Home from './pages/Home';
import Padlet from './pages/Padlet';
import LRCPolicy from './pages/LRCPolicy';
import EpicInstruction from './pages/EpicInstruction';
import DestinyInstruction from './pages/DestinyInstruction.js';
import LRCFAQs from './pages/LRCFAQs';
// import Policy1 from './pages/Policy1';
import LRCTeam from './pages/LRCTeam';
import Gallery from './pages/Lightbox';
import ContactUs from './pages/ContactUs';
import Footer from './pages/Footer';

export const routes = [
    {
        path: "/",
        exact: true,
        main: () => <Home />
    },
    {
        path: "/home",
        main: () => <Home />
    },
    {
        path: "/padlet",
        main: () => <Padlet />
    },
    {
        path: "/policy",
        main: () => <LRCPolicy />
    },
    {
        path: "/epicinstruction",
        main: () => <EpicInstruction />
    },
    {
        path: "/destinyinstruction",
        main: () => <DestinyInstruction />
    },
    {
        path: "/faqs",
        main: () => <LRCFAQs />
    },
    {
        path: "/team",
        main: () => <LRCTeam />
    },
    {
        path: "/gallery",
        main: () => <Gallery />
    },
    {
        path: "/contactus",
        main: () => <ContactUs />
    },
    {
        path: "/footer",
        main: () => <Footer />
    },
]