import React, { Component } from 'react';
import '../../style/LRCFAQs.scss';

export class LRCFAQs extends Component {
    render() {
        return (
            <div>
                <div className="lrc_faqs">
                    <div className="lrc_faqs_header roboto" data-aos="zoom-in">
                        <h6>LRC</h6>
                        <h1>FAQ's</h1>
                    </div>
                    
                    <iframe className="lrc_faqs_iframe" data-aos="zoom-in"
                        title="lrc_faqs_iframe"
                        src="https://docs.google.com/document/d/e/2PACX-1vQQyw2XoWWqFqzl9ShlurGvKmsW6_cCzVCgX4hwCi2qm9_eovq-NlHPAQeL8AiwJ590VXvmn9iiI-5b/pub?embedded=true"
                        width="100%"
                        height="100%"
                        frameBorder="0"
                    >
                    </iframe>

                    <iframe className="lrc_faqs_iframe_drive" data-aos="zoom-in"
                        title="lrc_faqs_iframe_drive"
                        src="https://drive.google.com/file/d/1T0f96JZOtILiXRdK0maeaqRRo8rG__7Y/preview"
                        width="100%"
                        frameBorder="0"
                    ></iframe>
                </div>   
            </div>
        )
    }
}

export default LRCFAQs
