import React, { Component } from 'react';
import '../../style/EpicInstruction.scss';

export class EpicInstruction extends Component {
    render() {
        return (
            <div>
                <div className="epic_instruction">
                    <div className="epic_instruction_header roboto" data-aos="zoom-in">
                        <h6>Login Instruction</h6>
                        <h1>EPIC LIBRARY</h1>
                    </div>
                    
                    <iframe className="epic_instruction_iframe" data-aos="zoom-in"
                        title="epic_instruction_iframe"
                        src="https://docs.google.com/document/d/e/2PACX-1vRcROT2qRfG0kK0nzEeZKI-2M6f6GHVVmD7BHSNDoWGvCHS7AMbgeR663otHwXAePpmqLJZa4nxIYjm/pub?embedded=true"
                        width="100%"
                        height="100%"
                        frameBorder="0"
                    >
                    </iframe>

                    <iframe className="epic_instruction_iframe_drive" data-aos="zoom-in"
                        title="epic_instruction_iframe_drive"
                        src="https://drive.google.com/file/d/1eAp64jjH9SmddyZk_7UrZNX93-XTa3pV/preview"
                        width="100%"
                        frameBorder="0"
                    ></iframe>
                </div>   
            </div>
        )
    }
}

export default EpicInstruction
