import React, { Component } from 'react';
import '../../style/LRCPolicy.scss';

export class LRCPolicy extends Component {
    render() {
        return (
            <div>
                <div className="lrc_policy">
                    <div className="lrc_policy_header roboto" data-aos="zoom-in">
                        <h1>LRC DIStance Learning</h1>
                        <h6>Policy</h6>
                    </div>
                    
                    <iframe className="lrc_policy_iframe" data-aos="zoom-in"
                        title="lrc_policy_iframe"
                        src="https://docs.google.com/document/d/e/2PACX-1vS2evZS_-MDfhsDRvbE_Z8bhhb_HXqe7i-lCYQ14Y7bfosKz7PTJA7S7Y2jijci0Tdks6hDy3kzytOv/pub?embedded=true"
                        width="100%"
                        height="100%"
                        frameBorder="0"
                    >
                    </iframe>

                    <iframe className="lrc_policy_iframe_drive" data-aos="zoom-in"
                        title="lrc_policy_iframe_drive"
                        src="https://drive.google.com/file/d/1q6yY0txKQmhlNTbTDEyIdGostsrW2Yos/preview"
                        width="100%"
                        frameBorder="0"
                    ></iframe>
                </div>                
            </div>
        )
    }
}

export default LRCPolicy
