import React, { Component } from 'react';
import '../../style/DestinyInstruction.scss';

export class DestinyInstruction extends Component {
    render() {
        return (
            <div>
                <div className="destiny_instruction">
                    <div className="destiny_instruction_header roboto" data-aos="zoom-in">
                        <h6>Login Instruction</h6>
                        <h1>DESTINY LIBRARY</h1>
                    </div>
                    
                    <iframe className="destiny_instruction_iframe" data-aos="zoom-in"
                        title="destiny_instruction_iframe"
                        src="https://drive.google.com/file/d/1RYYVhd1YaMERwGetNNCbp60ULrMiS_tT/preview"
                        width="100%"
                        frameBorder="0"
                    >
                    </iframe>
                </div>
            </div>
        )
    }
}

export default DestinyInstruction
