import React, { Component } from 'react';
import { MDBIcon } from "mdbreact";
import "../../style/footer.scss";
import DestinyLogo from "../../images/destiny.png"

export class Footer extends Component {
    render() {
        return (
            <div className="footer_body">
                <div className="upper_footer">

                    <div className="footer_logo footer_column">
                        <div className="dis_footer_logo">
                            <a href="https://domuschola.edu.ph" target="_blank" rel="noopener noreferrer">
                                <img src="https://domuschola.edu.ph/assets/img/logos/DISlogo2.png" alt="Domuschola International School" />
                            </a>
                        </div>
                    
                        <div className="affiliated">
                            <div className="affiliated_text">
                                Affiliated with:
                            </div>
                            <div className="cism_footer_logo">
                                <a href="https://www.cismanila.org/" target="_blank" rel="noopener noreferrer">
                                    <img src="https://domuschola.edu.ph/assets/img/footer/CISMlogo_affiliated.png" alt="CISM" />
                                </a>
                            </div>
                            <div className="ib_footer_logo">
                                <a href="https://www.ibo.org/" target="_blank" rel="noopener noreferrer">
                                    <img src="https://domuschola.edu.ph/assets/img/footer/IBlogo_affiliated.svg" alt="IB" />
                                </a>
                            </div>
                            <div className="deped_footer_logo">
                                <a href="http://www.deped.gov.ph/" target="_blank" rel="noopener noreferrer">
                                    <img src="https://domuschola.edu.ph/assets/img/footer/Depedlogo_affiliated.png" alt="Deped" />
                                </a>
                            </div>
                        </div>
                    </div>
                    

                    <div className="online_libraries footer_column roboto">
                        <h2>Online Libraries</h2>
                        
                        <div className="epic_footer_link">
                            <a href="https://www.getepic.com/students" target="_blank" rel="noopener noreferrer">
                                <img src="https://www.getepic.com/assets/epic-logo-blue-solid.svg" alt="getepic!" />
                            </a>
                        </div>
                        <div className="destiny_footer_link">
                            <a href="https://search.follettsoftware.com/metasearch/ui/71362" target="_blank" rel="noopener noreferrer">
                                <img src={DestinyLogo} alt="destiny discover" />
                            </a>
                        </div>        
                    </div>


                    <div className="contact_details footer_column roboto">
                        <div className="address">
                            <div className="address_icon">
                                <MDBIcon icon="map-marker-alt" />
                            </div>
                            <div>
                                13 Jose C.Cruz, Pasig
                                <p>Metro Manila</p>
                            </div>
                        </div>

                        <div className="contact">
                            <div className="contact_icon">
                                <MDBIcon icon="phone" />
                            </div>
                            <div>
                                (02) 8635 2002
                                <p>Mon - Fri, 8:00-22:00</p>
                            </div>
                        </div>

                        <div className="email">
                            <div className="email_icon">
                                <MDBIcon icon="envelope" />
                            </div>
                            <div>
                                info@domuschola.edu.ph
                            </div>
                        </div>
                    </div>
                </div>
                
                
                <div className="bottom_most_footer">
                    <div className="social_medias roboto">
                        <div>
                            <a href="https://www.facebook.com/domuscholaschool" target="_blank" rel="noopener noreferrer">
                                <MDBIcon fab className="icon" icon="facebook" />
                            </a>
                        </div>
                        <div>
                            <a href="https://www.instagram.com/domuschola" target="_blank" rel="noopener noreferrer">
                                <MDBIcon fab className="icon" icon="instagram" />
                            </a>
                        </div>
                        <div>
                            <a href="https://twitter.com/domuscholais" target="_blank" rel="noopener noreferrer">
                                <MDBIcon fab className="icon" icon="twitter" />
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Footer
