import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Burger from './Burger';
import '../style/Menu.scss';

export class Menu extends Component {
    
    openNav = () => {
        // document.getElementById("popup_menu").style.display = "block";
        document.getElementById("popup_menu").style.height = "100%";
    }
    
    closeNav() {
        // document.getElementById("popup_menu").style.display = "none";
        document.getElementById("popup_menu").style.height = "0%";
    }

    showHideInstructions = () => {
        var display = "block";

        if (document.getElementById("epic_instruction").style.display === "block") {
            display = "none";
        }
        
        document.getElementById("epic_instruction").style.display = display;
        document.getElementById("destiny_instruction").style.display = display;
        
    }
    
    render() {
        return (            
            <div>
                <div id="popup_menu" className="popup_menu">
                    <div className="close_btn" onClick={this.closeNav}>&times;</div>
                    
                    <div className="libraries">
                        <h1>Libraries</h1>
                        
                        <a href="https://www.getepic.com/students" target="_blank" rel="noopener noreferrer">
                            epic!
                        </a>
                        <a href="https://search.follettsoftware.com/metasearch/ui/71362" target="_blank" rel="noopener noreferrer">
                            Destiny Discover
                        </a>
                        
                        <hr/>
                    </div>

                    <div className="menu_link">
                        <Link className="link_text" onClick={this.closeNav} to="/">Home</Link>
                        <Link className="link_text" onClick={this.closeNav} to="/policy">Policy</Link>
                        <div id="login_instruction" className="link_text" onClick={this.showHideInstructions}>Login Instruction
                            <Link id="epic_instruction" className="link_text" onClick={this.closeNav} to="/epicinstruction">Get Epic Online</Link>
                            <Link id="destiny_instruction" className="link_text" onClick={this.closeNav} to="/destinyinstruction">Destiny Discover</Link>
                        </div>
                        <Link className="link_text" onClick={this.closeNav} to="/faqs">LRC FAQs</Link>
                        <Link className="link_text" onClick={this.closeNav} to="/gallery">Gallery</Link>
                        <Link className="link_text" onClick={this.closeNav} to="/contactus">Contact Us</Link>
                    </div>
                </div>

                 <div className="burger" onClick={this.openNav}>
                    <Burger />
                </div>
            </div>
        )
    }
}

export default Menu
